/**
 * Import custom Bootstrap source
 */
/**
 * General
 */
body[class*=single-billy-] .entry-header,
body[class*=single-billy-] .entry-title,
body[class*=single-billy-] .author-bio,
body[class*=single-billy-] .post-meta {
  display: none;
}
body[class*=single-billy-] .entry-content {
  font-family: inherit;
}
body[class*=single-billy-] [class*=show-on-] {
  display: none;
}
body[class*=single-billy-].single-billy-invoice .show-on-invoice, body[class*=single-billy-].single-billy-quote .show-on-quote, body[class*=single-billy-].single-billy-accounting .show-on-accounting {
  display: block;
}

img {
  vertical-align: middle;
}

mark {
  background-color: #ffc107;
}

/* Header */
.pre-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
  padding: 15px 0;
  border-bottom: 1px dashed;
}
.pre-header .wp-block-button {
  margin: 0 15px 0 0 !important;
}

.header {
  margin-bottom: 100px;
}
.header p {
  margin: 0;
}
.header .wp-block-image {
  margin: 0 !important;
}
.header .wp-block-image img {
  max-height: 110px;
  width: auto;
}
.header .wp-block-columns {
  margin-top: 0;
  margin-bottom: 0;
}

.details p {
  margin: 0;
}
.details .wp-block-column:last-of-type div {
  display: flex;
  font-size: smaller;
}
.details .wp-block-column:last-of-type div div {
  flex-basis: 50%;
  flex-grow: 0;
  padding: 0 0 0.5rem;
  line-height: 1.15;
  hyphens: auto;
}
.details .wp-block-column:last-of-type div .label {
  font-weight: bold;
  padding-right: 1rem;
  padding-left: 1rem;
}

.intro {
  margin-top: 100px;
  margin-bottom: 100px;
}

.information {
  margin-top: 100px;
}
.information p {
  font-size: smaller;
}

/* Tables */
#billy-accounting table tr[data-quarter=Q1] th {
  background-color: #F8F8F8;
}
#billy-accounting table tr[data-quarter=Q2] th {
  background-color: #E0E0E0;
}
#billy-accounting table tr[data-quarter=Q3] th {
  background-color: #D0D0D0;
}
#billy-accounting table tr[data-quarter=Q4] th {
  background-color: #B8B8B8;
}

.wp-block-table {
  width: 100%;
  margin-top: 60px;
  margin-bottom: 60px;
  border: none;
}
.wp-block-table th {
  cursor: pointer;
  white-space: nowrap;
}
.wp-block-table th::after {
  content: "";
  display: inline-block;
  width: 10px;
  height: 10px;
  margin-top: -5px;
  margin-left: 10px;
  border: none;
  border-bottom: 1px solid #0d6efd;
  border-left: 1px solid #0d6efd;
  transform: rotate(-225deg);
  transition: all 0.3s ease-in-out;
  visibility: hidden;
}
.wp-block-table th.asc::after {
  visibility: visible;
  transform: rotate(-225deg);
}
.wp-block-table th.desc::after {
  visibility: visible;
  transform: rotate(-45deg);
}
.wp-block-table th.earnings {
  color: #198754;
  background-color: #aff0d2;
}
.wp-block-table th.expenses {
  color: #dc3545;
  background-color: #f5c4c9;
}
.wp-block-table tbody {
  border-bottom: 1px solid var(--bs-border-color);
}
.wp-block-table tbody th,
.wp-block-table tbody td {
  vertical-align: top;
}
.wp-block-table tfoot {
  border: none;
}
.wp-block-table tfoot tr {
  border: none;
}
.wp-block-table tfoot tr.subtotal td, .wp-block-table tfoot tr.taxrates td {
  font-weight: initial !important;
}
.wp-block-table tfoot tr td {
  border-top: 2px solid;
  font-weight: bold;
  text-align: initial;
  white-space: nowrap;
}
.wp-block-table tfoot tr td.align-right {
  border: none;
  text-align: right;
}
.wp-block-table tfoot tr td.sum {
  text-align: center;
}
.wp-block-table tfoot tr td.sum.earnings, .wp-block-table tfoot tr td.sum.tax-earnings {
  background-color: #aff0d2;
}
.wp-block-table tfoot tr td.sum.expenses, .wp-block-table tfoot tr td.sum.tax-expenses {
  background-color: #f5c4c9;
}
.wp-block-table tfoot tr td.profit {
  position: relative;
  text-align: center;
}
.wp-block-table tfoot tr td.profit::before, .wp-block-table tfoot tr td.profit::after {
  content: "";
  display: block;
  width: 1rem;
  height: 1rem;
  position: absolute;
  top: 0;
  transform-origin: center center;
  z-index: -2;
  border-top: 2px dotted #0d6efd;
}
.wp-block-table tfoot tr td.profit::before {
  left: 15%;
  border-left: 2px dotted #0d6efd;
  transform: rotate(-90deg);
}
.wp-block-table tfoot tr td.profit::after {
  right: 15%;
  border-right: 2px dotted #0d6efd;
  transform: rotate(90deg);
}

#pdf {
  width: 50%;
  min-width: 400px;
  min-height: 300px;
  padding: 0 10px;
}
#pdf > a {
  margin: 0 10px 10px 0;
  display: inline-block;
}
#pdf .wp-block-file__embed {
  width: 100% !important;
  height: 300px;
}
#pdf .wp-block-file__embed:hover {
  height: 600px;
}

/* Print */
@media print {
  body[class*=single-billy-] {
    font-size: 9pt;
  }
  body[class*=single-billy-] #pdf,
body[class*=single-billy-] .pre-header,
body[class*=single-billy-] header,
body[class*=single-billy-] footer,
body[class*=single-billy-] #header,
body[class*=single-billy-] #footer,
body[class*=single-billy-] aside,
body[class*=single-billy-] nav,
body[class*=single-billy-] .entry-header,
body[class*=single-billy-] .entry-title,
body[class*=single-billy-] .author-bio,
body[class*=single-billy-] .post-meta {
    display: none;
  }
  .header {
    margin-bottom: 20pt;
  }
  .header .wp-block-image img {
    max-height: 90pt;
  }
  .wp-block-columns {
    flex-wrap: nowrap;
    flex-shrink: 1;
  }
  .details {
    margin-bottom: 0;
  }
  .details .wp-block-column:last-of-type {
    display: table;
  }
  .details .wp-block-column:last-of-type p {
    display: table-row;
    font-size: smaller;
  }
  .details .wp-block-column:last-of-type p * {
    display: table-cell;
  }
  .details .wp-block-column:last-of-type p strong {
    white-space: nowrap;
  }
  .intro {
    margin-top: 20pt;
    margin-bottom: 20pt;
  }
  .wp-block-table {
    font-size: 10pt;
  }
}